/*
Name:       Base16 Atelier Sulphurpool Light
Author:     Bram de Haan (http://atelierbram.github.io/syntax-highlighting/atelier-schemes/sulphurpool)
Prism template by Bram de Haan (http://atelierbram.github.io/syntax-highlighting/prism/)
Original Base16 color scheme by Chris Kempson (https://github.com/chriskempson/base16)
*/
code[class*="language-"],
pre[class*="language-"] {
  font-family: Consolas, Menlo, Monaco, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", "Courier New", Courier, monospace;
  font-size: 13px;
  line-height: 1.375;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  -moz-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  color: #5e6687;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
  text-shadow: none;
  background: #dfe2f1;
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
  text-shadow: none;
  background: #dfe2f1;
}

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: .1em;
  border-radius: .3em;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #898ea4;
}

.token.namespace {
  opacity: .7;
}

.token.operator,
.token.boolean,
.token.number {
  color: #c76b29;
}

.token.property {
  color: #c08b30;
}

.token.tag {
  color: #3d8fd1;
}

.token.function {
  color: #6f42c1;
}

.token.string {
  color: #22a2c9;
}

.token.selector {
  color: #6679cc;
}

.token.attr-name {
  color: #c76b29;
}

.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #22a2c9;
}

.token.attr-value,
.token.keyword,
.token.control,
.token.directive,
.token.unit {
  color: #d73a49;
}

.token.statement,
.token.regex,
.token.atrule {
  color: #22a2c9;
}

.token.placeholder,
.token.variable {
  color: #3d8fd1;
}

.token.deleted {
  text-decoration: line-through;
}

.token.inserted {
  border-bottom: 1px dotted #202746;
  text-decoration: none;
}

.token.italic {
  font-style: italic;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.important {
  color: #c94922;
}

.token.entity {
  cursor: help;
}

pre > code.highlight {
  outline: 0.4em solid #c94922;
  outline-offset: .4em;
}

/* overrides color-values for the Line Numbers plugin
 * http://prismjs.com/plugins/line-numbers/
 */
.line-numbers .line-numbers-rows {
  border-right-color: #dfe2f1;
}

.line-numbers-rows > span:before {
  color: #979db4;
}

/* overrides color-values for the Line Highlight plugin
 * http://prismjs.com/plugins/line-highlight/
 */
.line-highlight {
  background: rgba(107, 115, 148, 0.2);
  background: -webkit-linear-gradient(left, rgba(107, 115, 148, 0.2) 70%, rgba(107, 115, 148, 0));
  background: -webkit-gradient(linear, left top, right top, color-stop(70%, rgba(107, 115, 148, 0.2)), to(rgba(107, 115, 148, 0)));
  background: linear-gradient(to right, rgba(107, 115, 148, 0.2) 70%, rgba(107, 115, 148, 0));
}
.markdown-only{
  padding: 8px 0 16px;
}

.markdown-only h1,
.markdown-only h2{
  color:#212121;
}

.markdown-only h3{
  color:#424242;
}

.markdown-only.dark  h1,
.markdown-only.dark  h2{
  color:#EEEEEE;
}

.markdown-only.dark  h3{
  color:#E0E0E0;
}

.markdown-only code[class*="language-"], 
.markdown-only pre[class*="language-"]{
  color: #616161;
}

.markdown-only.dark code[class*="language-"], 
.markdown-only.dark pre[class*="language-"]{
  color: #E0E0E0;
}

.markdown-only table.grid {
  border-collapse: collapse;
  width: 100%;
  color: #757575;
  font-size: 14px;
  line-height: 1.5em;
}

.markdown-only.dark table.grid{
  color: #E0E0E0;
}

.markdown-only strong {
  font-weight: normal;
}

.markdown-only td, 
.markdown-only th {  
  padding: 10px;
  max-width: 250px;
  border-bottom: 1px solid #E0E0E0;
}

.markdown-only.dark td, 
.markdown-only.dark th { 
  border-bottom-color:  #424242;
}

.markdown-only th {
  text-align: left;
  white-space: nowrap;
  border-bottom-width: 2px;
}

.markdown-only th:first-child, 
.markdown-only td:first-child {
  padding-left: 10px;
}

.markdown-only .demo-box {
  position: relative;
}

.markdown-only .demo-box .source {
  padding: 48px 24px 24px;
  display: flex;
  justify-content: center;
  background-color: #eee;
}

.markdown-only.dark .demo-box .source{
  background-color: #212121;
}

.markdown-only .demo-box .meta {
  position: relative;
  overflow: hidden;
  background-color: #fff;
}

.markdown-only.dark .demo-box .meta {
  background-color: #424242;
}

.markdown-only .demo-box .highlight {
  overflow: auto;
  border-right: 1px solid #eee;
}

.markdown-only .demo-box .description {
  padding: 48px 18px 24px;
  width: 270px;
  height: 100%;
  font-size: 14px;
  line-height: 1.8;
  color: #757575;
  margin-left: -1px;
  word-break: break-word;
  box-sizing: border-box;
  border-left: 1px solid #E0E0E0;
  position: absolute;
  right: 0;
  z-index: 1;
}

.markdown-only.dark .demo-box .description{
  color: #E0E0E0;
  border-color: #616161;
}

.markdown-only .demo-box .description p {
  margin: 0;
}

.markdown-only .demo-box .description code {
  margin: 0 4px;
  color: #757575;
  background-color: #E0E0E0;  
  display: inline-block;
  padding: 1px 5px;
  font-size: 12px;
  border-radius: 3px;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.markdown-only.dark .demo-box .description code{
  color: #E0E0E0;
  background-color:#616161 ;  
}


.markdown-only .demo-box .demo-block-control {
  top: 0;
  right: 8px;
  display: flex;
  z-index: 10;
  position: absolute;
}

.markdown-only .demo-box .control-button {
  flex: 0 0 auto;
  width: 48px;
  height: 48px;
  padding: 12px;
  fill:#616161;
  background-color: transparent;
  text-align: center;
  border-radius: 50%;
  border:none;
  -webkit-transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  outline:none;
}

.markdown-only.dark .demo-box .control-button{
  fill:#E0E0E0;
}

.markdown-only .demo-box .control-button:hover{
  background-color: rgba(0, 0, 0, 0.08);
}


@media (max-width: 768px){
  .markdown-only .demo-box .description{
    display: none;
  }
}
